import React, { ReactNode, useContext, useState } from 'react'
import { useQueryClient } from 'react-query';
import * as auth from '../auth-provider'
import { User } from '../types/user'
import { FullPageErrorFallBack, FullPageLoading } from '../components/lib';
import { useMount } from '../utils';
import { http } from '../utils/http';
import { useAsync } from '../utils/use-async';

interface AuthForm {
  username: string;
  password: string;
}

// token
// bootstrap
const bootstrapUser = async () => {
  let user = null;
  // localstoragetoken
  const token = auth.getToken();
  if (token) {
    // token
    // tokenuseHttphttp
    const data = await http('me', { token });

    // user = data.user;
    user = data;
  }
  return user;
}

// React.createContextcontext
export const AuthContext = React.createContext<{
  user: User | null,
  register: (form: AuthForm) => Promise<void>
  login: (form: AuthForm) => Promise<void>
  logout: () => Promise<void>
} | undefined>(undefined)
AuthContext.displayName = 'AuthContext'


// React.ReactNoderender
export const AuthProvider = ({ children }: { children: ReactNode }) => {
  // useStateinitialState--
  // setUser
  // const [user, setUser] = useState<User | null>(null);
  const { data: user, error, isLoading, isIdle, isError, run, setData: setUser } = useAsync<User | null>()
  // 
  const queryClient = useQueryClient()
  // point free
  // (user) => setUser(user)  setUser
  const login = (form: AuthForm) => auth.login(form).then(setUser);
  const register = (form: AuthForm) => auth.register(form).then(setUser);
  const logout = () => auth.logout().then(() => {
    setUser(null);
    // 
    queryClient.clear()
  });

  // AuthProvideruser
  useMount(() => {
    run(bootstrapUser())
  })

  if (isIdle || isLoading) {
    return <FullPageLoading />
  }
  if (isError) {
    return <FullPageErrorFallBack error={error} />
  }
  //  Context  Provider React  context 
  // context <MyContext.Provider>  value prop 
  return <AuthContext.Provider children={children} value={{ user, login, register, logout }} />
}


// useAuthuser({ user, login, register, logout })
export const useAuth = () => {
  // React.useContextcontextcontext
  // <MyContext.Provider>Hook
  //  MyContext provider  context value 
  const context = React.useContext(AuthContext);
  if (!context) {
    throw new Error('useAuthAuthProvider')
  }
  return context;
}