import { useEffect, useRef, useState } from "react"

// 0
export const isFalsy = (value: unknown) => value === 0 ? false : !value


// 
// input

// obj: objectobjectfunc
// export const cleanObject = (obj: object) => {
export const cleanObject = (obj: { [key: string]: unknown }) => {
  // 
  // Object.assign({}, obj);
  const result = { ...obj };
  Object.keys(result).forEach(key => {
    // @ts-ignore
    const value = result[key];
    if (isVoid(value)) {
      delete result[key];
    }
  })
  return result;
}

// 
// {checkfalse}
export const isVoid = (value: unknown) =>
  value === undefined ||
  value === null ||
  value === ''

// 
// useEffect
export const useMount = (callback: () => void) => {
  useEffect(() => {
    callback()
    // TODO callback
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])
}


// 
// valuedelaydebouncedValuedebouncedValue
// useStatevaluehook
export const useDebounce = <V>(value: V, delay?: number) => {
  const [debouncedValue, setDebouncedValue] = useState(value);
  useEffect(() => {
    // value
    const timeOut = setTimeout(() => setDebouncedValue(value), delay)
    //  effect  effect 
    // 
    // 
    return () => clearTimeout(timeOut);
  }, [value, delay])
  return debouncedValue
}

// title
// keepOnUnmount
//truetitle
//falsetitle
export const useDocumentTitle = (title: string, keepLastTitlt: boolean = true) => {
  // useRef: .currentref
  // oldTitletitle
  const oldTitle = useRef(document.title).current  //reactApp
  // title
  // title
  useEffect(() => {
    document.title = title
  }, [title])
  // titlekeepOnUnmounttruetitletitle
  // title
  useEffect(() => {
    // keepOnUnmountfalsetitletitle
    return () => {
      if (!keepLastTitlt) {
        document.title = oldTitle;
      }
    }
  }, [keepLastTitlt, oldTitle])
}


// 
export const resetRoute = () => window.location.href = window.location.origin

/**
 * 
 * false
 * true
 */
export const useMountedRef = () => {
  const mountRef = useRef(false);
  useEffect(() => {
    mountRef.current = true;
    return () => {
      mountRef.current = false;
    }
  })
  return mountRef;
}

/**
 * 
 * @param obj
 * @param keys
 */
 export const subset = <
 O extends { [key in string]: unknown },
 K extends keyof O
>(
 obj: O,
 keys: K[]
) => {
 const filteredEntries = Object.entries(obj).filter(([key]) =>
   keys.includes(key as K)
 );
 return Object.fromEntries(filteredEntries) as Pick<O, K>;
};

