import qs from 'qs'
import { useCallback } from 'react';

import * as auth from '../auth-provider'
import { useAuth } from '../context/auth-context';
const apiUrl = process.env.REACT_APP_API_URL;

// fetch
interface ConfigRequestInit extends RequestInit {
  data?: object,
  token?: string,
}

// 
// endPoint
// 
export const http = async (
  endPoint: string,
  { data, token, headers, ...customConfig }: ConfigRequestInit = {}
) => {
  const config = {
    method: 'GET',
    headers: {
      Authorization: token ? `Bearer ${token}` : '',
      'Content-Type': data ? 'application/json' : ''
    },
    // method:'POST'
    ...customConfig
  }

  // get
  // postbody
  if (config.method.toUpperCase() === 'GET') {
    endPoint += `?${qs.stringify(data)}`
  } else {
    config.body = JSON.stringify(data || {})
  }

  return window
    .fetch(`${apiUrl}/${endPoint}`, config)
    .then(async response => {
      // token
      if (response.status === 401) {
        // 
        await auth.logout();
        // 
        window.location.reload();
        return Promise.reject({ message: '' })
      }
      // 401
      const data = await response.json()
      if (response.ok) {
        return data
      } else {
        // response.okfalsefetchajax
        return Promise.reject(data)
      }
    })
}

// JWT token
// useCarryTokenehttptokenbody
export const useHttp = () => {
  const { user } = useAuth();
  // Parameters<typeof http> ts
  // Parameterstsutility Types
  // typeof httphttpfunction
  // Parameters
  return useCallback((...[endPoint, config]: Parameters<typeof http>) => {
    return http(endPoint, { ...config, token: user?.token })
  }, [user?.token])
}
